import { Component } from 'react';

class LangText extends Component {
  constructor(props) {
    super(props);
    let texts = {
        'login_text': {
          'eng': 'To start work you need to join or create your session',
          'rus': '          '
        },
        'email_text' : {
          'eng': 'Enter email',
          'rus': ' Email'
        },      
        'password' : {
          'eng': 'Password',
          'rus': ''
        },
        'role' : {
          'eng': 'Role',
          'rus': ''
        },   
        'cam1' : {
          'eng': 'First camera',
          'rus': ' '
        },
        'cam2' : {
          'eng': 'Second camera',
          'rus': ' '
        },    
        'cam3' : {
          'eng': 'Third camera',
          'rus': ' '
        }, 
        'director' : {
          'eng': 'Director',
          'rus': ''
        },    
        'language' : {
          'eng': 'Language',
          'rus': ''
        },
        'create_ses' : {
          'eng': 'Create session',
          'rus': ' '
        },
        'enter_ses' : {
          'eng': 'Enter session',
          'rus': ''
        },    
        'exit_ses' : {
          'eng': 'Exit session',
          'rus': '  '
        },
        'changing' : {
          'eng': 'changing',
          'rus': ' '
        },    
        'cam_text' : {
          'eng': 'Select current Shot',
          'rus': '  '
        }    
    };
    this.state = { out: texts[props.str][props.lang] || props.str }
  }
 
  render() {
    return this.state.out;
  }  
    
}

export default LangText;


