import React, { Component } from 'react';
import Camera from './Camera';
import LangText from './Lang';

class Table extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      role: props.role,  
      cameras: {
        eng: ["Director", "1st camera", "2nd camera", "3rd camera"],
        rus: ["", " ", " ", " "]
      }
    };
  }  
  
  render() {
    return (
      <div className="container">
        <div className="row">
          {this.props.role !== 0 ?
          <div className="col-md cam-slot">
            <Camera name={this.state.cameras[this.props.lang][parseInt(this.state.role, 10)]} db={this.props.db} role={this.props.role} num={parseInt(this.state.role, 10)} lang={this.props.lang}  />
          </div> : ''      
          }
          {this.state.cameras[this.props.lang].map((cam, idx) => 
            idx !== parseInt(this.state.role, 10) && idx > 0 ? 
            <div className="col-md cam-slot" key={idx}>
              <Camera name={cam} collapsed={this.props.role !== 0} db={this.props.db} num={idx} role={this.props.role} lang={this.props.lang} />
            </div> : ''  
          )}
        </div>

        <div className="row pt-3">
          <div className="col d-flex flex-row-reverse">
            <button name="logout" className="btn btn-secondary" onClick={this.props.logout_handle}><LangText str="exit_ses" lang={this.props.lang} /></button>
          </div>
        </div>
      </div>
    );
  }
}

export default Table;


